<template>
<div>
<el-row :gutter="40">
  <el-col :span="6">
  <div class="grid-content bg-purple" v-for="y in yes" :key="y.id">
  <div class="card" @click="heupload(y.id)">
    <img :src="y.headImage" class="image">
    <div class="namee">{{y.name}}</div>
  </div>
  </div>
  </el-col>
  <el-col :span="6">
    <div class="grid-content bg-purple" v-for="n in no" :key="n.id">
      <div class="card" @click="heupload(n.id)">
      <img :src="n.headImage" class="image">
      <div class="namee">{{n.name}}</div>
    </div>
    </div>
  </el-col>
</el-row>
</div>
</template>

<script>
export default {
  name: 'home',
  props:['number','cate'],
  data () {
    return {
      data: '',
      yes:'',
      no:''
    }
  },
  mounted() {
      let _this = this
     if(_this.cate != '') {
       _this.$http({
        method: 'post',
        url: '/weekly/user/getUserList.action',
        params: {
          'id': _this.number,
          'category': _this.cate
        }
    })
    .then(function(res){
       _this.data = res.data
      _this.yes = res.data.data[0].yes
      _this.no = res.data.data[1].no
    })
    .catch(function(err){
      console.log(err)
    })
    }
  },
  methods:{
    heupload(idd){
    {//此人学号
      this.$emit('showup',idd);
      this.$router.push('otherupload');
    }
    }
  }
  // mounted(){
  //   let _this = this
  //   _this.$http({
  //       method: 'post',
  //       url: '/api/weekly/user/getUserList.action',
  //       params: {
  //         'id': _this.number,
  //         'category': _this.cate
  //       }
  //   })
  //   .then(function(res){
  //     _this.data = res.data
  //     _this.yes = res.data.data[0].yes
  //     _this.no = res.data.data[1].no
  //   })
  //   .catch(function(err){
  //     console.log(err)
  //   })
  // }
  // watched:{
  //   cate:function(){
  //      let _this = this_this.$http({
  //       method: 'post',
  //       url: '/api/weekly/user/getUserList.action',
  //       params: {
  //         'id': _this.number,
  //         'category': _this.cate
  //       }

  //   })
    // .then(function(res){
    //     _this.data = res.data
    //   })
    //   .catch(function(err){
    //     console.log(err)
    //   })
  //   } 
  // }
}
</script>

<style>
.image{
  width: 100%;
  display:block;
}

</style>



